.container {
    width: 1200px;
    margin: 0 auto;
}

.whiteBg {
    background-color: $white !important;
    box-shadow: 0 5px 30px rgba(0,0,0,0.1);
    & a {
        color: $black !important;
    }
}

.transparent {
    background: transparent;

    & a {
        color: $white;
    }
}

.pl-15{
    padding-left: 15px;
}

.mt-60{
    margin-top: 60px;
}

.h-350{
    height: 350px;
}